<template>
  <div id="home">
    <div id="sleep" v-if="sleep">
      <button id="btn-sleep" v-on:click="wakeUp"></button>
    </div>
    <router-link  v-if="!sleep" to="/qa/0" class="button"></router-link>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      sleep: true
    }
  },
  created() {
    this.sleep = true
    this.$emit('sleep', true)
  },
  methods: {
    wakeUp() {
      this.sleep = false
      this.$emit('sleep', false)
    }
  }
}
</script>

