<template>
  <div id="result" v-bind:style="{ 'background-image': 'url(' + backgroundImage + ')' }">
    <div id="score">{{score}}</div>
    <router-link to="/" class="button"></router-link>
  </div>
</template>

<script>
export default {
  name: 'Result',
  props: ['score'],
  data() {
    return {
      backgroundImage: null
    }
  },
  created() {
    if (this.score <= 2)
      this.backgroundImage = require('@/assets/img/'+this.$lang+'/result-bad.png')
    else if (this.score > 2 && this.score <= 5)
      this.backgroundImage = require('@/assets/img/'+this.$lang+'/result-average.png')
    else if (this.score > 5 && this.score <= 7)
      this.backgroundImage = require('@/assets/img/'+this.$lang+'/result-good.png')
    else if (this.score > 7)
      this.backgroundImage = require('@/assets/img/'+this.$lang+'/result-excellent.png')
  }
}
</script>

<style lang="scss" scoped>
#result{
  width: 100%;
  height: 100%;
  background-image: url('~@/assets/img/fr/result-average.png');
}
#score{
  position: absolute;
  top: 564px;
  right: 395px;
