<template>
  <div id="app">
    <Menu v-if="menuVisible"/>
    <router-view @sleep="showMenu"/>
  </div>
</template>

<script>
import Menu from '@/components/Menu.vue'

export default {
  name: 'App',
  components: {
    Menu
  },
  data() {
    return {
      menuVisible: false
    }
  },
  methods: {
    showMenu(sleep) {
      if(sleep)
        this.menuVisible = false
      else
        this.menuVisible = true
    }
  }
}
</script>

