<template>
  <div id="qa">
    <img :src="background" :alt="background" class="background">
    <ul class="questions">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <Tunnel ref="tunnel" :index="this.index" :score="this.score"/>
  </div>
</template>

<script>
import Tunnel from '@/components/Tunnel.vue'

export default {
  name: 'Qa',
  props: ['index'],
  components: {
    Tunnel
  },
  data() {
    return {
      qa: null,
      background: null,
      answersActive: true,
      result: true,
      score: null
    }
  },
  created() {
    fetch('./data/qa.json')
    .then(r => r.json())
    .then(json => {
      this.qa = json.qa
      this.setBackground(0)
    })

    // Rinit le score en cas de nouvelle partie
    this.score = 0
  },
  watch: {
    $route() {
      this.setBackground(0)
      this.answersActive = true
      this.$refs.tunnel.showNextButton = false
    }
  },
  methods: {
    setBackground(index) {
      this.background = require('@/assets/img/'+this.$lang+'/qa-'+this.index+'-'+index+'.png')
    },
    selectAnswer(index) {
      if(this.answersActive) {
        this.setBackground(index)
        this.result = this.qa[this.index].answers[index-1]
        // Dsactive les buttons rponse une fois qu'on a rpondu
        this.answersActive = false
        // Update Tunnel
        this.$refs.tunnel.updateDot(this.index, this.result)
        // Update score
        if(this.result == true)
          this.score ++
      }      
    }
  }
}
</script>

